
export default {
  name: "ProfileMainInfo",
  emits: {
    "update-name": null,
  },
  props: {
    profile: Object,
    getAnswerDate: Function,
  },
  data() {
    return {
      isChangeName: false,
      isChangeGroup: false,
    };
  },
  methods: {
    toggleChangeName() {
      this.isChangeName = !this.isChangeName;
    },
    updateName(newName) {
      this.$emit("update-name", newName);
    },
  },
};
