

import {Axios} from "../assets/js/http-common";

export default {
  name: "RatingPage",
  data() {
    return {
      userRatings: null,
    };
  },
  created() {
    this.getUserRatings();
  },
  inject: ["deviceWidth"],
  props: {
    getPointsCountSuffix: Function,
  },
  methods: {
    async getUserRatings() {
      try {
        this.userRatings = await Axios.get("user-ratings/").then(({ data }) => data.ratings || []);
      } catch (error) {
        console.error(error);
      }
    },
  },

};
